rt Other Styles\r\n*********************/\r\n\r\n// @import '~themify-icons/css/themify-icons.css';\r\n@import '~font-awesome/css/font-awesome.css';\r\n@import 'variable';\r\n@import 'app';\r\n@import 'spinner';\r\n\r\n/* Purple Theme */\r\n.purple {\r\n    @import 'colors/purple.scss';\r\n    @include angular-material-theme($purple-app-theme);\r\n}\r\n\r\n/* Green Theme */\r\n.green {\r\n    @import 'colors/green.scss';\r\n    @include angular-material-theme($green-app-theme);\r\n}\r\n\r\n/* Red Theme */\r\n.red {\r\n    @import 'colors/red.scss';\r\n    @include angular-material-theme($red-app-theme);\r\n}\r\n\r\n/* Brown Theme */\r\n.brown {\r\n    @import 'colors/brown.scss';\r\n    @include angular-material-theme($brown-app-theme);\r\n}\r\n","/*********************\r\nMain Styles And Import Other Styles\r\n*********************/\n@import url(~font-awesome/css/font-awesome.css);\n/*******************\r\nGlobal Variables\r\n*******************/\n@import url(\"https://fonts.googleapis.com/css?family=Helvetica Neue:300,400,500,600,700,800\");\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\n/*******************\r\nmaterial theme colors\r\n*******************/\n@import url(~@angular/material/prebuilt-themes/indigo-pink.css);\n@import url(~perfect-scrollbar/css/perfect-scrollbar.css);\n@import url(~@swimlane/ngx-datatable/index.css);\n@import url(~@swimlane/ngx-datatable/themes/material.css);\n@import url(~@swimlane/ngx-datatable/assets/icons.css);\n.mat-badge-content {\n  font-weight: 600;\n  font-size: 12px;\n  font-family: Roboto, \"Helvetica Neue\", sans-serif;\n}\n\n.mat-badge-small .mat-badge-content {\n  font-size: 9px;\n}\n\n.mat-badge-large .mat-badge-content {\n  font-size: 24px;\n}\n\n.mat-h1, .mat-headline, .mat-typography h1 {\n  font: 400 24px/32px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n  margin: 0 0 16px;\n}\n\n.mat-h2, .mat-title, .mat-typography h2 {\n  font: 500 20px/32px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n  margin: 0 0 16px;\n}\n\n.mat-h3, .mat-subheading-2, .mat-typography h3 {\n  font: 400 16px/28px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n  margin: 0 0 16px;\n}\n\n.mat-h4, .mat-subheading-1, .mat-typography h4 {\n  font: 400 15px/24px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n  margin: 0 0 16px;\n}\n\n.mat-h5, .mat-typography h5 {\n  font: 400 calc(14px * 0.83)/20px Roboto, \"Helvetica Neue\", sans-serif;\n  margin: 0 0 12px;\n}\n\n.mat-h6, .mat-typography h6 {\n  font: 400 calc(14px * 0.67)/20px Roboto, \"Helvetica Neue\", sans-serif;\n  margin: 0 0 12px;\n}\n\n.mat-body-strong, .mat-body-2 {\n  font: 500 14px/24px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n}\n\n.mat-body, .mat-body-1, .mat-typography {\n  font: 400 14px/20px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n}\n\n.mat-body p, .mat-body-1 p, .mat-typography p {\n  margin: 0 0 12px;\n}\n\n.mat-small, .mat-caption {\n  font: 400 12px/20px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n}\n\n.mat-display-4, .mat-typography .mat-display-4 {\n  font: 300 112px/112px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: -0.05em;\n  margin: 0 0 56px;\n}\n\n.mat-display-3, .mat-typography .mat-display-3 {\n  font: 400 56px/56px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: -0.02em;\n  margin: 0 0 64px;\n}\n\n.mat-display-2, .mat-typography .mat-display-2 {\n  font: 400 45px/48px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: -0.005em;\n  margin: 0 0 64px;\n}\n\n.mat-display-1, .mat-typography .mat-display-1 {\n  font: 400 34px/40px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n  margin: 0 0 64px;\n}\n\n.mat-bottom-sheet-container {\n  font: 400 14px/20px Roboto, \"Helvetica Neue\", sans-serif;\n  letter-spacing: normal;\n}\n\n.mat-button, .mat-raised-button, .mat-